import React, { Component } from 'react'

export default class Punkts extends Component {
  render() {
    return (
      <div className='mainConteiner'>
        <h2 className="punktsTitle">Тут ви можете знайти найближчий пункт вакцинації
        та перевірити наявність потрібної вам вакццини
        </h2>
        <div className="punktsMap">
        <iframe width="425" height="350" frameborder="0" scrolling="no" marginheight="0"  src="https://www.openstreetmap.org/export/embed.html?bbox=30.411357879638675%2C50.40063997404632%2C30.5720329284668%2C50.47280190471659&amp;layer=mapnik" ></iframe>
        <input placeholder='Введіть ваше місто' className='punktsInput' type="text" />
        </div>
      </div>
    )
  }
}
