import React, { Component } from 'react'
import addIcon from './img/add-48.png'
import ListVac from './ListVac'



export default class Main extends Component {
  constructor(props) {
    super(props)
    this.renderChooseVak = this.renderChooseVak.bind(this)
    this.state = {
      number:0,
      array: [],
      lastArrayName:this.props.names,
      lastArrayDate:this.props.dates
    }
  }

renderChooseVak() {

}

render() {
  let arr1 = this.props.names.map((item,i)=>
  {
    if(i<2)
    {
      return <ListVac blue='blueBlock' key={i} name={item} data={'Рекомендована дата: '+this.props.dates[i]}/>
    }
    else{
      return <ListVac blue='' key={i} name={item} data={this.props.dates[i]}/>
    }
  }
  )
  return (
    <div className='mainConteiner'>
      <h2 className="mainTitle">
        Привіт.<br />
        Раді вітати тебе в новому сервісі для відслідковування вакцин!
      </h2>
      <p className="mainText">
        Сподіваємося що зможемо допомогти тобі та облегшити твоє життя.
        Тут ти зможеш додати одразу декілька акаунтів, щоб дивитись не тільки свої
        вакцини, а й вакцини своєї родини
      </p>
      <div className="mainLine"></div>
      <div className="arrayVac">
      {
        arr1
      }
      </div>
      <button onClick={this.props.click} className='mainAddBtn'><img src={addIcon} alt="addIcon" /></button>
    </div>
  )
}
}
