import React, { Component } from 'react'
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import data from './img/userinfo.json'


let link = 'https://632af52e1090510116cce103.mockapi.io/healhhak/1';




export default class LogIn extends Component {
    
    constructor(props){
        super(props);
        this.logInChange = this.logInChange.bind(this)
        this.passwordChange = this.passwordChange.bind(this)
        this.logInClick = this.logInClick.bind(this)
        this.state = {
            logInValue:'admin',
            passwordValue:'admin'
        }
    }
    logInChange(e)
    {
        this.setState({logInValue: e.target.value})
    }
    passwordChange(e)
    {
        this.setState({passwordValue: e.target.value})
    }
    logInClick()
    {
        console.log(this.state.logInValue)
        console.log(this.state.passwordValue)
        if(data.user===this.state.logInValue && data.password===this.state.passwordValue)
        {
            this.props.setSt()
        }
        else
        {
            console.log('Loh')
        }
        
    }
    render() {       
        return (
            <div className='login__form'>
                <h2 className='login__title'>Вхід</h2>
                <div className='inputField'>
                    <TextField
                        value={this.state.logInValue}
                        onChange={this.logInChange}
                        required
                        id="outlined-required"
                        label="E-mail"
                    //   defaultValue="E-mail"
                    />
                </div>
                <div className='inputField'>
                    <TextField
                        value={this.state.passwordValue}
                        onChange={this.passwordChange}
                        id="outlined-password-input"
                        label="Password"
                        type="password"
                        autoComplete="current-password"
                    />
                </div>
                <Button onClick={this.logInClick} variant="contained" color='success'>Log In</Button>
            </div>
        )
    }
}
