import { IconButton } from '@mui/material'
import React, { Component } from 'react'
import avatar from './img/pngwing.com.png'
import PersonAddIcon from '@mui/icons-material/PersonAdd';

class UsersMenu extends Component {
    constructor(props) {
        super(props)

        this.state = {
        }
    }
    render() {
        let newArray = this.props.usersName.map((item, i) => {
            return <div className="subUsersBlock" key={i}>
                <img src={this.props.usersAva[i]} alt="i" className="subUserAva" />
                <h4>{item}</h4>
            </div>

        })
        return (
            <div className='usersMenu'>
                <img src={avatar} alt="avatar" className="usersAvatar" />
                <h3 className="usersTitle">Дівущак Мартин</h3>
                <div className="usersSubtitle">qwerty@gmail.com</div>
                <div className="usersLine"></div>
                <div className="usersBlock">
                    {
                        newArray
                    }
                </div>
                <div className="addNewUser">
                    <IconButton aria-label="upload picture" component="label">
                        <PersonAddIcon fontSize='large' />
                    </IconButton>
                </div>
                <button onClick={this.props.exit} className="btnExit">Вихід</button>
            </div>
        );
    }
}

export default UsersMenu;