import { Button, IconButton } from '@mui/material'
import React, { Component } from 'react'
import CancelIcon from '@mui/icons-material/Cancel';
import InfoAboutVac from './InfoAboutVac';
import data from './infoVac.json'
import BlockInfo from './BlockInfo';

export default class ModalAddVac extends Component {
  constructor(props) {
    super(props)
    this.chooseVak = this.chooseVak.bind(this)
    this.showChooseVac = this.showChooseVac.bind(this)
    this.changeDate = this.changeDate.bind(this)
    this.addClickBtn=this.addClickBtn.bind(this)
    this.state = {
      nameVac: '',
      date: ''
    }
  }
  changeDate(e) {
    this.setState({ date: e.target.value })
  }
  chooseVak(e) {
    this.setState({ nameVac: e.target.value })

  }
  showChooseVac() {
    if (this.state.nameVac !== '') {
      return <BlockInfo name={this.state.nameVac} changeInput={this.changeDate} />
    }
  }
  addClickBtn()
  {
    this.props.clickAdd(this.state.nameVac, this.state.date)
  }
  render() {
    return (
      <div className="addWindow">
        <div className='addWindowVac'>
          <h3 className="addVakTitle">Оберіть вакцину:</h3>
          <select className='chooseVac' name="select" onChange={this.chooseVak}>
            <option value=""></option>
            <option value="Гепатит В">Гепатит В</option>
            <option value="Кір, Паротит, Краснуха">Кір, Паротит, Краснуха</option>
            <option value="Дифтерія">Дифтерія</option>
            <option value="Поліомеліт">Поліомеліт</option>
            <option value="Коронавірус">Коронавірус</option>
            <option value="Туберкульоз">Туберкульоз</option>
            <option value="Кашлюк">Кашлюк</option>
          </select>
          {
            this.showChooseVac()
          }

          <div className="mainBtnClose">
            <IconButton onClick={this.props.clickClose}>
              <CancelIcon></CancelIcon>
            </IconButton>
          </div>
          <div className="mainBtnAdd">
            <Button className='btn_auto' color='success' variant="contained" onClick={this.addClickBtn}>Додати</Button>
          </div>

        </div>
      </div>
    )
  }
}
