import React, { Component } from 'react'
import { Link, Routes, Route } from "react-router-dom";
import Account from './Account';
import Flowers from './Flowers';
import avatarka from './img/pngwing.com.png'
import Info from './Info';
import Main from './Main';
import Punkts from './Punkts';
import ModalAddVac from './ModalAddVac';
import UsersMenu from './UsersMenu';
import ava1 from './img/ava1.png'
import ava2 from './img/ava2.png'
import ava3 from './img/ava3.png'
export default class MainWindow extends Component {
    constructor(props) {
        super(props)
        this.clickAddVac = this.clickAddVac.bind(this)
        this.showModalWindow = this.showModalWindow.bind(this)
        this.clickCloseModal = this.clickCloseModal.bind(this)
        this.addNewVac = this.addNewVac.bind(this)
        this.showUsersMenu=this.showUsersMenu.bind(this)
        this.clickUsersMenu=this.clickUsersMenu.bind(this)
        this.state = {
            number: 0,
            menuNumber: false,
            namesVac: ['Рекомендована вакцина 1','Рекомендована вакцина 2'],
            dates: ['10.10.22','28.10.22'],
            usersName:['Мурга Катерина','Дівущак Юлія','Дівущак Олександр'],
            usersAva:[ava1,ava2,ava3]
        }
    }
    clickAddVac() {
        this.setState({ number: 1 })
    }
    clickAddVacLast() {
        this.setState({ number: 0 })
    }
    clickCloseModal() {
        this.setState({ number: 0 })
    }
    clickUsersMenu()
    {
        this.setState({menuNumber: !this.state.menuNumber})
    }
    showModalWindow() {
        if (this.state.number === 0) {
            return
        }
        else if (this.state.number === 1) {
            return <ModalAddVac clickClose={this.clickCloseModal} clickAdd={this.addNewVac} clickCloseModal={this.clickCloseModal} />
        }
    }
    addNewVac(name, data) {
        let arr1 = this.state.namesVac
        arr1.push(name)
        let arr2 = this.state.dates
        arr2.push(data)
        this.setState({
            namesVac: arr1,
            dates: arr2
        })
        this.clickAddVacLast()
    }
    showUsersMenu()
    {
        if(this.state.menuNumber)
        {
            return <UsersMenu usersName={this.state.usersName} usersAva={this.state.usersAva} exit={this.props.exit}/>
        }
    }
    render() {
        return (
            <div className='mainWindow'>
                <header className='header'>
                    <Link className='mainLink' to='/'>Головна</Link>
                    <Link className='mainLink' to='/info'>Інформація про вакцини</Link>
                    <Link className='mainLink' to='/flowers'>Квітка життя</Link>
                    <Link className='mainLink' to='/punkts'>Пункти вакцинації</Link>
                    <Link className='mainLink' to='/acount'>Аккаунт</Link>
                    <button onClick={this.clickUsersMenu} className="openUsers"><img className='imgAvatar' src={avatarka} alt="avatar" /></button>
                </header>
                <div className="subUsers">
                    
                </div>
                <div className="black__line"></div>
                <div className="mainContent">
                    <Routes>
                        <Route path='/*' element={<Main names={this.state.namesVac} dates={this.state.dates} click={this.clickAddVac} clickTwo={this.clickAddVacTwo} clickClose={this.clickCloseModal} name={this.state.namesVac} data={this.state.dates} />} />
                        <Route path='/*/info' element={<Info />} />
                        <Route path='/*/flowers' element={<Flowers />} />
                        <Route path='/*/punkts' element={<Punkts />} />
                        <Route path='/*/acount' element={<Account />} />
                    </Routes>
                </div>
                {
                    this.showModalWindow()
                }
                {
                    this.showUsersMenu()
                }
            </div>
        )
    }
}
