import React from 'react';
import Button from '@mui/material/Button';

const Btns = (props) => {
    return (
        <div className="btn__block">
            <Button className='btn_auto' color='success' variant="contained" onClick={props.logInClick}>Вхід</Button>
            <Button className='btn_auto' color='success' variant="contained" onClick={props.signUpClick}>Реєстрація</Button>
        </div>

    );
};

export default Btns;