import React, { Component } from 'react';
import data from './infoVac.json'
class BlockInfo extends Component {
    constructor(props) {
        super(props)
        this.showInfoAboutVac = this.showInfoAboutVac.bind(this)
        this.showInfoAboutVacTwo = this.showInfoAboutVacTwo.bind(this)
        this.state = {
            data
        }
    }
    showInfoAboutVac() {
        for (let i in data) {
            if (this.props.name === data[i].name) {
                return data[i].info
            }
        }

    }
    showInfoAboutVacTwo() {
        for (let i in data) {
            if (this.props.name === data[i].name) {
                return data[i].info2
            }
        }

    }

    render() {
        return (
            <div>
                <h4 className='blockInfoSubtitle'>Інформація про вакцину</h4>
                <p>{
                    this.showInfoAboutVac()
                }</p>
                <p className='blockInfoText' >Введіть дату вакцинації:</p>
                <input onChange={this.props.changeInput} className='blockInfoInput' type="text" />
                <h4 className='blockInfoSubtitle'>Побічна реакція:</h4>
                <p>{
                    this.showInfoAboutVacTwo()
                }</p>
            </div>
        );
    }
}

export default BlockInfo;