import React, { Component } from 'react'
import Button from '@mui/material/Button';
import Maintext from './OldComponents/Maintext';
import LogIn from './LogIn';
import SignUp from './SignUp';
import Btns from './Btns';
import MainWindow from './MainWindow';
import { BrowserRouter } from 'react-router-dom';
export default class Container extends Component {
    constructor(props) {
        super(props);
        this.logInClick = this.logInClick.bind(this)
        this.logInClickTwo = this.logInClickTwo.bind(this)
        this.signUpClick = this.signUpClick.bind(this)
        this.showContent = this.showContent.bind(this)
        this.exit=this.exit.bind(this)
        this.state =
        {
            number: 0
        };
    }
    logInClick() {
        this.setState({ number: 1 })
    }
    logInClickTwo() {
        this.setState({
            number: 3
        })
    }
    signUpClick() {
        this.setState({ number: 2 })
    }
    exit()
    {
        this.setState({ number: 0 })
    }
    showContent() {
        if (this.state.number === 0) {
            return <Btns logInClick={this.logInClick} signUpClick={this.signUpClick} />
        }
        else if (this.state.number === 1) {
            return <LogIn number={this.state.number} setSt={this.logInClickTwo} />
        }
        else if (this.state.number === 2) {
            return <SignUp signUpClick={this.signUpClick} />
        }
        else if (this.state.number === 3) {
            return <MainWindow exit={this.exit} />
        }
    }
    render() {
        return (
            <BrowserRouter>
                <div className='container'>
                    <div className="content">
                        {
                            this.showContent()
                        }
                    </div>
                </div>
            </BrowserRouter>
        )
    }
}
